* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: sans-serif;
}

.btn-2 {
    background: none;
    padding: 1rem 1.2rem;
    border: none;
    font-size: 1.5rem;
}

.dager {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;


}

.Joining-Netflix h1 {
    margin: 1rem auto;
    width: 50%;
    font-size: 2.6rem;
    font-weight: 600;

}

input {
    margin: 1rem 0;
    border: 1px solid gray;
    padding: 1rem 4rem;
    border-radius: 4px;
    font-size: 1.2rem;
}
.dager p:nth-child(0){
    margin: 1rem 0;
     width: 25%;
     font-size: 1.2rem;
}
.btn {
    border: none;
    padding: 1rem 9rem;
    border-radius: 4px;
    background-color: red;
    color: #fff;
    font-size: 1.4rem;
}

a {
    text-decoration: none;
}

.footer-2 {
    display: flex;
    justify-content: space-evenly;
    margin: 1rem 0;
}

.footer-part {
    display: flex;
}

@media(max-width:700px) {

    .footer-1 h1{
        margin: 1rem 0;
        font-size: 1.2rem;
    }
    .Joining-Netflix h1 {
        font-weight: 300;
        width: 70%;
        font-size: 1.9rem;
        color: red;
    }
    input{
        border: 1px solid gray;
        padding: 1rem 2rem;
        border-radius: 4px;
        font-size: 1.rem;
    }
    .btn {
       
        padding: 1rem 7rem;
        border-radius: 4px;
        background-color: red;
        color: #fff;
        font-size: 1.3rem;
    }
    .dager p:nth-child(0){
        
         width: 30%;
         font-size: 1.1rem;
    }
    
}
@media(max-width:500px) {

    
    .Joining-Netflix h1 {
        font-weight: 300;
        width: 70%;
        font-size: 1.6rem;
        color: red;
    }
}
@media(max-width:400px) {
    .dager p:nth-child(0){
        
        width: 45%;
        font-size: 1.1rem;
   }
}