.full-section {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    background: #000;
    color: #fff;
    font-family: 'Roboto', sans-serif;
}

.full-section p {
    width: 50%;
    margin: 2rem 0;
    font-family: 'Roboto', sans-serif;
}

.list-section {
    display: flex;
}

.list-section li {
    list-style: none;
    margin: 0 1rem;
    font-size: 1.5rem;
    font-weight: 300;
}

@media(max-width:540px) {
    .list-section li{
        font-size: 1rem;
        font-weight: 86;
    }.full-section p {
        width: 85%;
       
    }
}
@media(max-width:416px){
    .list-section li{
        font-size: .9rem;
        font-weight: 80;
    }
}